import React from 'react';
import PropTypes from 'prop-types';
import Panel from '@vkontakte/vkui/dist/components/Panel/Panel';
import PanelHeader from '@vkontakte/vkui/dist/components/PanelHeader/PanelHeader';
import {Button, Group, CellButton} from '@vkontakte/vkui';

import Cell from '@vkontakte/vkui/dist/components/Cell/Cell';
import Div from '@vkontakte/vkui/dist/components/Div/Div';
import Avatar from '@vkontakte/vkui/dist/components/Avatar/Avatar';
import PanelHeaderButton from "@vkontakte/vkui/dist/components/PanelHeaderButton/PanelHeaderButton";
import PanelHeaderContent from "@vkontakte/vkui/dist/components/PanelHeaderButton/PanelHeaderButton";
import {IOS, platform} from "@vkontakte/vkui";
import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
import Icon24Back from '@vkontakte/icons/dist/24/back';
import Icon24Add from '@vkontakte/icons/dist/24/add';

import logo from "../img/logo.png";
import './logoheader.css';

const osName = platform();

const ClientForm = ({ id, go, fetchedUser }) => (
	<Panel id={id}>
		<PanelHeader
			left={<PanelHeaderButton onClick={go} data-to="client">
				{osName === IOS ? <Icon28ChevronBack/> : <Icon24Back/>}
			</PanelHeaderButton>}
		><img class="LogoHeader" src={logo} alt="RE-DO-ME" /></PanelHeader>


		<Group title="Navigation Example">
			<Div>
				<CellButton before={<Icon24Add />}>Загрузите ваше фото</CellButton>
			</Div>
			<Div>
				<CellButton before={<Icon24Add />}>Загрузите желаемое фото</CellButton>
			</Div>
			<Div>
				<Button size="xl" level="2" onClick={go} data-to="persik">
					Найти похожую звезду
				</Button>
			</Div>
			<Div>
				<Button size="xl" level="2" onClick={go} data-to="persik">
					Поиск мастера
				</Button>
			</Div>
		</Group>
	</Panel>
);

ClientForm.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
	fetchedUser: PropTypes.shape({
		photo_200: PropTypes.string,
		first_name: PropTypes.string,
		last_name: PropTypes.string,
		city: PropTypes.shape({
			title: PropTypes.string,
		}),
	}),
};

export default ClientForm;
